
import headTop from '@/components/header'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import Api from '@/js/common'

export default {
data() {
return {
sortId:'',
questiontypeId: '', //题目类型id
questionType: '', //题目类型
question: '', //题目
list: [], //题目类型数组
index: 1, //题目选项序号
answer: [{ index: 1, value: '' }],
mustAnswer: true, //必答
selectShow: true 
}
},
computed: {
...mapGetters(['device'])
},
methods: {
addAnswer() {
this.index = this.index + 1
var answerList = { index: this.index, value: '' }
this.answer.push(answerList)
},
onChange(val) {
this.questiontypeId = val
if (val == 1) {
this.selectShow = true
this.questionType = '单选题'
} else if (val == 2) {
this.selectShow = true
this.questionType = '多选题'
} else if (val == 3) {
this.selectShow = true
this.questionType = '下拉题'
} else if (val == 4) {
this.questionType = '单行文本'
this.selectShow = false
this.answer = [{ index: 1, value: '' }]
} else if (val == 5) {
this.questionType = '多行文本'
this.selectShow = false
this.answer = [{ index: 1, value: '' }]
}
},
del(index) {
var vmSelf = this
this.$vux.confirm.show({

title: '操作提示',
content: '确定删除吗？',
onCancel() {},
onConfirm() {

for (var i in vmSelf.answer) {
if (vmSelf.answer[i].index == index) {
vmSelf.answer.splice(i, 1)
}
}
}
})
},
submitInfo() {
if (!this.question) {
this.alertInfo('请填写问卷标题')
return
}
var questionInfo = {}
if (
this.questiontypeId == 1 ||
this.questiontypeId == 2 ||
this.questiontypeId == 3
) {
var answer = {}
let judgeAnswer = []
var answerIndex = 0
for (var i in this.answer) {
if (this.answer[i].value != '') {
var value1 = { text: this.answer[i].value }
answer[answerIndex] = value1
judgeAnswer.push(value1)
answerIndex++
}
}
if (judgeAnswer.length <= 1) {
this.alertInfo('至少两个选项')
return
}
questionInfo = {

id: '',
name: this.question, //题目标题
type: this.questiontypeId, //题目类型
options: answer, //选项
status: this.mustAnswer ? 0 : 1, //是否必选
sort_id: Api.getVal('questionIndex')
}
} else {
questionInfo = {

id: '',
name: this.question, //题目标题
type: this.questiontypeId, //题目类型
options: this.answer, //选项
status: this.mustAnswer ? 0 : 1, //是否必选
sort_id: Api.getVal('questionIndex')
}
}
var questionList = Api.getVal('questionList')
? Api.getVal('questionList')
: '[]'
console.log(111,questionList)
questionList = JSON.parse(questionList)
var infoType = Api.getVal('quesAction')
if (infoType == 'edit') {

var quesListIndex = Api.getVal('quesActionIndex')
questionList[quesListIndex] = questionInfo
if (this.$route.query.id) {

questionInfo.id = this.$route.query.id
var editObj = Api.getVal('editObj') ? Api.getVal('editObj') : '[]'
editObj = JSON.parse(editObj)

let editLength = editObj.length-1
if(editLength>=0){
for (var i in editObj) { //判断editObj 是否有问题id
if (editObj[i].id == this.$route.query.id) {
editObj[i] = questionInfo
}
if(i == editLength && editObj[i].id != this.$route.query.id){
editObj.push(questionInfo)
}
}
}else{
editObj.push(questionInfo)
}
editObj = JSON.stringify(editObj)
Api.setVal('editObj', editObj)
} else {
delete questionInfo.id
var addObj = Api.getVal('addObj')? Api.getVal('addObj') : '[]'
addObj = JSON.parse(addObj)
let addLength = addObj.length-1
if(addLength>=0){
for (var i in addObj) { //判断editObj 是否有问题id
if (addObj[i].sort_id == this.sortId) {
addObj[i] = questionInfo
addObj = JSON.stringify(addObj)
Api.setVal('addObj', addObj)
}
}
}
}
} else {
questionList.push(questionInfo)
console.log('questionList',questionList)

delete questionInfo.id
var addObj = Api.getVal('addObj') ? Api.getVal('addObj') : '[]'
addObj = JSON.parse(addObj)
addObj.push(questionInfo)
addObj = JSON.stringify(addObj)
console.log('addObj',addObj)
Api.setVal('addObj', addObj)
}

questionList = JSON.stringify(questionList)
Api.setVal('questionList', questionList)
this.$router.go(-1)
},
getquestionType() {
var vmSelf = this
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.questionType()
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.list = response
})
.catch(function(err) {
console.log('=====', err)
})
},
alertInfo(title) {
this.$vux.alert.show({
title: '提示',
content: title,
onShow() {
console.log("Plugin: I'm showing")
},
onHide() {
console.log("Plugin: I'm hiding now")
}
})
}
},
mounted() {
this.getquestionType()
this.onChange(Api.getVal('questionId'))
var infoType = Api.getVal('quesAction')
if (infoType == 'edit') {

var quesListIndex = Api.getVal('quesActionIndex')
var questionList = Api.getVal('questionList')
this.sortId =  Api.getVal('sortId')
questionList = JSON.parse(questionList)
this.questiontypeId = questionList[quesListIndex].type
this.question = questionList[quesListIndex].name
var answer = []
var options = questionList[quesListIndex].options

if (typeof options == 'string') {
options = JSON.parse(options)
}
for (var i in options) {
var value1 = {
index: i,
value: options[i].text
}
answer[i] = value1
}
console.log("66666666",answer)
this.answer = answer

if(questionList[quesListIndex].status == 1){
this.mustAnswer = false
}else{
this.mustAnswer = true
}
}
appcan.setNavigationBarTitle({
title: "问题"
});
},
components: {
XTextarea,
headTop,
XInput,
XSwitch,
Group,
Selector,
XButton,
ViewBox
}
}



